<template>
  <div class="techer-wechat-wrap">
    <img
      class="techer-wechat__banner"
      src="@/assets/excellentManage/banner.png"
      alt="">
    <div class="content">
      <div
        class="item"
        v-for="(v, k) in classInfo"
        :key="k">
        <div class="qr" v-if="k == 0">
          <div class="qr_title">{{ className[v.sp] }} </div>
          <div
            class="wrap"
            v-for="item in v.sucess"
            :key="item.order_num">
            <div class="info">
              <div class="info_title">班主任二维码</div>
              <img
                :src="item.result.qrcode"
                alt=""
                class="info_qr" />
              <div class="info_tip">点击二维码，长按添加班主任</div>
            </div>
            <div class="detail">
              <div class="buy_time">
                购买时间：{{ item.create_time || "--" }}
              </div>
            </div>
          </div>
          <div
            :class="errInfoClass(v)"
            v-if="v.fail.length >= 0">
            <div
              class="item"
              v-for="item in v.fail"
              :key="item.order_num">
              <div class="order">
                <img
                  src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/Qrcode/warn.png"
                  alt=""
                  class="warn" />
                <span class="order_num">订单号:{{ item.order_num }}</span>
                <span
                  class="copy"
                  @click="
                    copy(item.order_num, {
                      pageName: '卓越管理者体验营-老师二维码页',
                      blockname: className[v.sp],
                      buttonname: item.result.msg,
                    })
                  ">复制</span>
              </div>
              <div class="order_info">
                {{ item.result.msg }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="techer-wechat__join" @click="followCourse">
      关注微淼大咖课，获取更多福利
      <img src="@/assets/excellentManage/back.png" alt="">
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Store from '@/store'
import { copyText, getSearch } from '@/utils'
import wxAuth from '@/helper/Auth/wxAuth'
export default {
  data () {
    return {
      info: null,
      list: {}
    }
  },
  computed: {
    ...mapState('user', ['className', 'classInfo']),
    errInfoClass () {
      return (item) => {
        return {
          err_info: true,
          active: item.fail.length > 0 && item.sucess.length > 0
        }
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    if (to.name !== 'ManageCheckPhone') {
      next({
        name: 'ManageCheckPhone'
      })
    }
    next()
  },
  async beforeRouteEnter (to, from, next) {
    await wxAuth({
      getTokenQuery: { ...getSearch() }
    })
    const { id } = Store.state.user.userInfo || {}
    Store.$log.update({ mid: id })
    next()
  },
  created () {
    wx.ready(() => {
      wx.hideAllNonBaseMenuItem()
    })
  },
  methods: {
    /**
     * @description: 关注微淼大咖课
     */
    followCourse () {
      window.location.href = 'https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=Mzk0NjIwMzEzNg==&scene=124#wechat_redirect'
    },
    /**
     * @description:复制订单信息
     */
    copy (orderNum) {
      copyText(orderNum, '复制成功')
    }

  }
}
</script>
<style lang="scss" scoped>
.techer-wechat-wrap {
  width: 100%;
  background: linear-gradient(360deg, #3363fe 0%, #3263fd 100%);
  overflow: hidden;

  .techer-wechat__banner {
    width: 100%;
    height: 352px;
  }
}

.content {
  .item {
    margin-bottom: 50px;

    .qr {
      width: 670px;
      padding-bottom: 40px;
      margin: 0 auto;
      background: #fff;
      border-radius: 10px;
      position: relative;
      padding-top: 100px;
      padding-left: 20px;
      padding-right: 20px;

      .active {
        // TODO
        border-top: 1px dashed #dce1e8;
      }

      .err_info {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 30px;
        padding: 30px;

        .item {
          width: 600px;
          height: 205px;
          background: #f8f8f7;
          border-radius: 12px;
          padding: 32px;
          margin-top: 20px;

          .order_info {
            height: 80px;
            font-size: 28px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #999;
            line-height: 40px;
            padding-left: 44px;
            margin-top: 20px;
          }

          .order {
            height: 45px;
            font-size: 32px;
            font-weight: 400;
            color: #333;
            font-family: PingFangSC-Regular, PingFang SC;
            line-height: 45px;
            display: flex;
            align-items: center;

            .warn {
              width: 36px;
              height: 36px;
              margin-right: 10px;
            }

            .order_num {
              flex-grow: 1;

              @include ellipsis;
            }

            .copy {
              width: 106px;
              margin-left: 20px;
              height: 40px;
              font-size: 28px;
              font-family: PingFangSC-Regular, PingFang SC;
              font-weight: 400;
              color: #1e66ed;
              line-height: 40px;
            }
          }
        }
      }

      .detail {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 20px;

        div {
          // width: 410px;
          height: 40px;
          font-size: 28px;
          font-weight: 400;
          color: #999;
          line-height: 40px;
          margin-top: 20px;
        }
      }

      .info {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        width: 457px;
        height: 554px;
        background: #f7f7f8;
        border-radius: 12px;
        margin: 0 auto;

        .info_title {
          font-size: 36px;
          font-weight: 600;
          color: #2a2f36ff;
        }

        .info_tip {
          font-size: 28px;
          font-weight: 400;
          color: #666;
          line-height: 40px;
        }

        .info_qr {
          width: 352px;
          height: 352px;
        }
      }

      .qr_title {
        width: 547px;
        height: 88px;
        background-image: url("https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/Qrcode/title_bg.png");
        background-size: contain;
        background-repeat: no-repeat;
        position: absolute;
        left: 50%;
        top: -20px;
        transform: translateX(-50%);
        font-size: 40px;
        font-weight: 500;
        color: #fff;
        line-height: 88px;
        text-align: center;
      }
    }
  }
}

.techer-wechat__des {
  font-size: 28px;
  color: #e5e8ff;

  @include horVerCenter;
}

.techer-wechat__join {
  width: 626px;
  height: 107px;
  background: linear-gradient(180deg, #fff 0%, #f4f5ff 31%, #aebaff 100%);
  border-radius: 54px;
  color: #1d2c99;
  font-size: 36px;
  margin: 48px auto 39px;

  @include horVerCenter;

  img {
    width: 15px;
    height: 25px;
    margin-left: 16px;
  }
}

</style>
